import { defineStore } from "pinia";

// adminStore
import adminStore from '@/stores/admin/adminStore';

export default defineStore('statusStore', {
  state: () => ({
    // api
    api: {
      url: 'https://vue3-course-api.hexschool.io/v2',
      path: 'vue2022ron',
    },
  }),
  getters: {
  },
  actions: {
    // sweet Alert2
    swAlert(pos, icon, title, showConfirmBtn, toast) {
      // Promise baseawait
      return new Promise((resolve, reject) => {
        resolve(
          this.Swal.fire({
            position: pos,    // 
            icon: icon,
            title: title,
            showConfirmButton: showConfirmBtn,
            timer: 2500,  // 
            toast: toast, // false: toast; true: alert
          })
        );
      })
    },
    swDelect(product) {
      this.Swal.fire({
        title: `${product.title}?`,
        text: "",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then((result) => {
        if (result.isConfirmed) {
          this.axios.delete(`${this.api.url}/api/${this.api.path}/admin/product/${product.id}`)
            .then((res) => {
              this.Swal.fire(
                res.data.message,
                '',
                'success'
              );
              const admin = adminStore();  // adminStore
              // (category)
              this.axios.get(`${this.api.url}/api/${this.api.path}/admin/products/?category=${admin.selectEl.value}`)
                .then(res => {
                  // 
                  admin.tempProducts = res.data.products;
                })
                .catch(err => {
                  this.swAlert('center', 'error', err.response.data.message, false, true);
                })
            })
            .catch((err) => {
              this.Swal.fire(
                err.response.data.message,
                '',
                'error'
              );
            });
        }
      })
    },
    // 
    logout() {
      this.Swal.fire({
        title: ``,
        text: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then((result) => {
        if (result.isConfirmed) {
          this.axios.post(`${this.api.url}/logout`)
            .then(res => {
              this.Swal.fire(
                res.data.message,
                '',
                'success'
              ).then((result) => {
                if (result.isConfirmed) {
                  // login
                  this.router.push('/login');
                }
              });
            })
            .catch(err => {
              this.Swal.fire(
                err.response.data.message,
                '',
                'error'
              );
            });
        }
      })
    },
  },
});